import { isString, ShapeFlags } from '@vue/shared'
import { getSequence } from './sequence'
import { createVnode, isSameVnode, Text, Fragment } from './vnode'

export function createRenderer(renderOptions) {
  let {
    inset: hostInset,
    remove: hostRemove,
    setElementText: hostSetElementText,
    setText: hostSetText,
    parentNode: hostParentNode,
    nextSiblint: hostNextSiblint,
    createElement: hostCreateElement,
    createText: hostCreateText,
    patchProp: hostPatchProp
  } = renderOptions

  const normalize = (children, i) => {
    if (isString(children[i])) {
      let vnode = createVnode(Text, null, children[i])
      children[i] = vnode
      return vnode
    }
    return children[i]
  }

  const mounthChildren = (children, container) => {
    for (let i = 0; i < children.length; i++) {
      let child = normalize(children, i)
      patch(null, child, container)
    }
  }

  const mountElement = (vnode, container, anchor = null) => {
    const { type, props, children, shapeFlag } = vnode
    let el = (vnode.el = hostCreateElement(type))
    if (props) {
      for (let key in props) {
        hostPatchProp(el, key, null, props[key])
      }
    }
    if (shapeFlag & ShapeFlags.TEXT_CHILDREBN) {
      // 文本
      hostSetElementText(el, children)
    } else if (shapeFlag & ShapeFlags.ARRAY_CHILDREN) {
      mounthChildren(children, el)
    }

    hostInset(el, container, anchor)
  }

  const processText = (n1, n2, container) => {
    if (n1 === null) {
      hostInset((n2.el = hostCreateText(n2.children)), container)
    } else {
      let el = (n2.el = n1.el)
      if (n1.children !== n2.children) {
        hostSetText(el, n2.children)
      }
    }
  }

  const patchProps = (oldProps, newProps, el) => {
    for (let key in newProps) {
      if (newProps[key]) {
        hostPatchProp(el, key, oldProps[key], newProps[key])
      }
    }
    for (let key in oldProps) {
      if (!newProps[key]) {
        hostPatchProp(el, key, oldProps[key], undefined)
      }
    }
  }

  const unmountChildren = children => {
    for (let i = 0; i < children.length; i++) {
      unmount(children[i])
    }
  }

  const pathKeydChildren = (c1, c2, el) => {
    let i = 0
    let e1 = c1.length
    let e2 = c2.length

    // sync from start
    while (i <= e1 && i <= e2) {
      const n1 = c1[i]
      const n2 = c2[i]
      if (isSameVnode(n1, n2)) {
        patch(n1, n2, el)
      } else {
        break
      }
      i++
    }

    // sync from end
    while (i <= e1 && i <= e2) {
      const n1 = c1[e1]
      const n2 = c2[e2]
      if (isSameVnode(n1, n2)) {
        patch(n1, n2, el)
      } else {
        break
      }
      e1--
      e2--
    }

    // 若i>e1  i - e2 之间的节点新增
    if (i > e1) {
      // 有新增
      if (i <= e2) {
        const nextPos = e2 + 1
        // 想知道往那个方向新增
        const anchor = nextPos < c2.length ? c2[nextPos].el : null
        while (i <= e2) {
          patch(null, c2[i], el, anchor)
          i++
        }
      }
    } else if (i > e2) {
      while (i <= e1) {
        unmount(c1[i])
        i++
      }
    }
    // 乱序比较
    let s1 = i
    let s2 = i
    const keyToNewIndexMap = new Map()
    for (let i = s2; i <= e2; i++) {
      keyToNewIndexMap.set(c2[i].key, i)
    }
    // 循环老节点， 若新的里面有则比较，若没有则删除
    const toBePacth = e2 - s2 + 1
    const newIndexToOldIndexMap = new Array(toBePacth).fill(0)
    for (let i = s1; i <= e1; i++) {
      const oldChild = c1[i]
      const flag = keyToNewIndexMap.has(oldChild.key)
      let newIndex = keyToNewIndexMap.get(oldChild.key)
      if (!flag) {
        unmount(oldChild)
      } else {
        newIndexToOldIndexMap[newIndex - s2] = i + 1
        patch(oldChild, c2[newIndex], el)
      }
    }
    const increment = getSequence(newIndexToOldIndexMap)

    let j = increment.length - 1
    // 移动位置
    for (let i = toBePacth - 1; i > 0; i--) {
      let index = i + s2
      let current = c2[index]
      let anchor = index + 1 < c2.length ? c2[index + 1].el : null
      if (newIndexToOldIndexMap[i] === 0) {
        patch(null, current, el, anchor)
      } else {
        if (i !== increment[j]) {
          hostInset(current.el, el, anchor)
        } else {
          j--
        }
      }
    }
  }

  const patchChildren = (n1, n2, el) => {
    const c1 = n1 && n1.children
    const c2 = n2 && n2.children
    const prevShapeFlag = n1.shapeFlag
    const shapeFlag = n2.shapeFlag

    if (shapeFlag & ShapeFlags.TEXT_CHILDREBN) {
      if (prevShapeFlag & ShapeFlags.ARRAY_CHILDREN) {
        unmountChildren(c1)
      }
      if (c1 !== c2) {
        hostSetElementText(el, c2)
      }
    } else {
      if (prevShapeFlag & ShapeFlags.ARRAY_CHILDREN) {
        if (shapeFlag & ShapeFlags.ARRAY_CHILDREN) {
          pathKeydChildren(c1, c2, el)
        } else {
          unmountChildren(c1)
        }
      } else {
        if (prevShapeFlag & ShapeFlags.TEXT_CHILDREBN) {
          hostSetElementText(el, '')
        }
        if (shapeFlag & ShapeFlags.ARRAY_CHILDREN) {
          mountElement(n2, el)
        }
      }
    }
  }

  // 复用节点  再比较属性，再比较子节点
  const patchElement = (n1, n2) => {
    let el = (n2.el = n1.el)
    let oldProps = n1.props || {}
    let newProps = n2.props || {}

    // 比较属性
    patchProps(oldProps, newProps, el)
    // 比较子节点
    patchChildren(n1, n2, el)
  }

  const processElement = (n1, n2, container, anchor) => {
    if (n1 === null) {
      mountElement(n2, container, anchor)
    } else {
      patchElement(n1, n2)
    }
  }

  const processFragment = (n1, n2, container) => {
    if (n1 === null) {
      mounthChildren(n2.children, container)
    } else {
      patchChildren(n1, n2, container)
    }
  }

  const patch = (n1, n2, container, anchor = null) => {
    if (n1 === n2) return
    if (n1 && !isSameVnode(n1, n2)) {
      unmount(n1)
      n1 = null
    }
    const { type, shapeFlag } = n2
    // 初次渲染
    // 目前处理元素的初次渲染
    switch (type) {
      case Text:
        processText(n1, n2, container)
        break
      case Fragment:
        processFragment(n1, n2, container)
        break
      default:
        if (shapeFlag & ShapeFlags.ELEMENT) {
          processElement(n1, n2, container, anchor)
        }
    }
  }

  const unmount = vnode => {
    hostRemove(vnode.el)
  }

  const render = (vnode, container) => {
    if (vnode === null) {
      if (container._vnode) {
        unmount(container._vnode)
      }
    } else {
      // 既做初始化也更新
      patch(container._vnode || null, vnode, container)
    }
    container._vnode = vnode
  }

  return {
    render
  }
}
